import React from 'react';
import { useOracle } from '../hooks/useOracle';

const PriceFeed: React.FC = () => {
  const { 
    prices, 
    loading, 
    error, 
    marketSummary, 
    refreshPrices,
    formatPrice,
    formatChange,
    formatVolume,
    formatMarketCap
  } = useOracle(true, 30000); // Auto-refresh every 30 seconds

  if (loading && prices.length === 0) {
    return (
      <div className="bg-gradient-to-r from-slate-800 to-slate-900 rounded-xl p-6 border border-slate-700">
        <div className="animate-pulse">
          <div className="h-6 bg-slate-700 rounded w-1/3 mb-4"></div>
          <div className="space-y-3">
            {[1, 2, 3].map((i) => (
              <div key={i} className="h-16 bg-slate-700 rounded"></div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-gradient-to-r from-red-900/20 to-red-800/20 rounded-xl p-6 border border-red-700/50">
        <div className="text-red-400 text-center">
          <div className="text-lg font-semibold mb-2">Oracle Error</div>
          <div className="text-sm mb-4">{error}</div>
          <button
            onClick={refreshPrices}
            className="px-4 py-2 bg-red-600 hover:bg-red-700 rounded-lg transition-colors"
          >
            Retry
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-gradient-to-r from-slate-800 to-slate-900 rounded-xl p-6 border border-slate-700">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <div>
          <h2 className="text-xl font-bold text-white">Price Feeds</h2>
          <p className="text-slate-400 text-sm">Live market data from Oracle</p>
        </div>
        <button
          onClick={refreshPrices}
          disabled={loading}
          className="px-3 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-slate-600 rounded-lg transition-colors text-sm font-medium"
        >
          {loading ? 'Refreshing...' : 'Refresh'}
        </button>
      </div>

      {/* Market Summary */}
      {marketSummary && (
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
          <div className="bg-slate-700/50 rounded-lg p-3">
            <div className="text-slate-400 text-xs">Total Market Cap</div>
            <div className="text-white font-semibold">{formatMarketCap(marketSummary.totalMarketCap)}</div>
          </div>
          <div className="bg-slate-700/50 rounded-lg p-3">
            <div className="text-slate-400 text-xs">24h Volume</div>
            <div className="text-white font-semibold">{formatVolume(marketSummary.totalVolume24h)}</div>
          </div>
          <div className="bg-slate-700/50 rounded-lg p-3">
            <div className="text-slate-400 text-xs">Top Gainer</div>
            <div className="text-green-400 font-semibold">{marketSummary.topGainers[0]}</div>
          </div>
          <div className="bg-slate-700/50 rounded-lg p-3">
            <div className="text-slate-400 text-xs">Top Loser</div>
            <div className="text-red-400 font-semibold">{marketSummary.topLosers[0]}</div>
          </div>
        </div>
      )}

      {/* Price Cards */}
      <div className="space-y-4">
        {prices.map((asset) => (
          <div
            key={asset.symbol}
            className="bg-slate-700/30 rounded-lg p-4 border border-slate-600 hover:border-slate-500 transition-colors"
          >
            <div className="flex items-center justify-between">
              {/* Asset Info */}
              <div className="flex items-center space-x-3">
                <div className="w-10 h-10 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center">
                  <span className="text-white font-bold text-sm">{asset.symbol[0]}</span>
                </div>
                <div>
                  <div className="text-white font-semibold">{asset.symbol}</div>
                  <div className="text-slate-400 text-sm">
                    Updated: {asset.lastUpdated.toLocaleTimeString()}
                  </div>
                </div>
              </div>

              {/* Price and Change */}
              <div className="text-right">
                <div className="text-white text-lg font-bold">
                  {formatPrice(asset.price)}
                </div>
                <div className={`text-sm font-medium ${
                  asset.changePercent24h >= 0 ? 'text-green-400' : 'text-red-400'
                }`}>
                  {formatChange(asset.changePercent24h, true)}
                </div>
              </div>

              {/* Market Data */}
              <div className="hidden md:block text-right space-y-1">
                <div className="text-slate-400 text-xs">Volume 24h</div>
                <div className="text-white text-sm">{formatVolume(asset.volume24h)}</div>
              </div>

              <div className="hidden lg:block text-right space-y-1">
                <div className="text-slate-400 text-xs">Market Cap</div>
                <div className="text-white text-sm">{formatMarketCap(asset.marketCap)}</div>
              </div>
            </div>

            {/* 24h Change Bar */}
            <div className="mt-3">
              <div className="flex items-center justify-between text-xs text-slate-400 mb-1">
                <span>24h Change</span>
                <span>{formatChange(asset.change24h)}</span>
              </div>
              <div className="w-full bg-slate-700 rounded-full h-2">
                <div
                  className={`h-2 rounded-full transition-all duration-300 ${
                    asset.changePercent24h >= 0 
                      ? 'bg-gradient-to-r from-green-500 to-green-400' 
                      : 'bg-gradient-to-r from-red-500 to-red-400'
                  }`}
                  style={{
                    width: `${Math.min(Math.abs(asset.changePercent24h) * 2, 100)}%`,
                    marginLeft: asset.changePercent24h >= 0 ? '0' : 'auto'
                  }}
                ></div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Last Updated */}
      <div className="mt-6 text-center">
        <div className="text-slate-500 text-xs">
          Last updated: {new Date().toLocaleString()}
        </div>
        <div className="text-slate-600 text-xs mt-1">
          Data refreshes automatically every 30 seconds
        </div>
      </div>
    </div>
  );
};

export default PriceFeed; 