// Mock Oracle for Price Feeds
// Provides realistic price data for NIBI, USDC, and ATOM

export interface PriceFeed {
  symbol: string;
  price: number;
  change24h: number;
  changePercent24h: number;
  volume24h: number;
  marketCap: number;
  lastUpdated: Date;
}

export interface OracleResponse {
  success: boolean;
  data: PriceFeed[];
  timestamp: number;
}

// Mock price data with realistic values
const MOCK_PRICES = {
  NIBI: {
    basePrice: 0.85,
    volatility: 0.15,
    volume: 2500000,
    marketCap: 85000000
  },
  USDC: {
    basePrice: 1.00,
    volatility: 0.001,
    volume: 50000000,
    marketCap: 50000000000
  },
  ATOM: {
    basePrice: 8.50,
    volatility: 0.25,
    volume: 15000000,
    marketCap: 3400000000
  }
};

// Generate realistic price fluctuations
function generatePriceFluctuation(basePrice: number, volatility: number): number {
  const randomChange = (Math.random() - 0.5) * 2 * volatility;
  return basePrice * (1 + randomChange);
}

// Generate 24h change
function generate24hChange(volatility: number): number {
  return (Math.random() - 0.5) * 2 * volatility;
}

// Mock Oracle Class
export class MockOracle {
  private lastUpdate: Date;
  private priceCache: Map<string, PriceFeed>;

  constructor() {
    this.lastUpdate = new Date();
    this.priceCache = new Map();
    this.initializePrices();
  }

  private initializePrices(): void {
    Object.entries(MOCK_PRICES).forEach(([symbol, config]) => {
      const price = generatePriceFluctuation(config.basePrice, config.volatility);
      const change24h = generate24hChange(config.volatility);
      
      this.priceCache.set(symbol, {
        symbol,
        price: parseFloat(price.toFixed(6)),
        change24h: parseFloat(change24h.toFixed(6)),
        changePercent24h: parseFloat(((change24h / config.basePrice) * 100).toFixed(2)),
        volume24h: config.volume + Math.random() * config.volume * 0.2,
        marketCap: config.marketCap + Math.random() * config.marketCap * 0.1,
        lastUpdated: new Date()
      });
    });
  }

  // Get current prices for all assets
  async getPrices(): Promise<OracleResponse> {
    // Simulate network delay
    await new Promise(resolve => setTimeout(resolve, 100 + Math.random() * 200));

    // Update prices with some randomness
    this.updatePrices();

    return {
      success: true,
      data: Array.from(this.priceCache.values()),
      timestamp: Date.now()
    };
  }

  // Get price for specific asset
  async getPrice(symbol: string): Promise<PriceFeed | null> {
    await new Promise(resolve => setTimeout(resolve, 50 + Math.random() * 100));
    
    this.updatePrices();
    return this.priceCache.get(symbol) || null;
  }

  // Get price in specific denomination
  async getPriceInDenom(symbol: string, denom: string): Promise<number | null> {
    const price = await this.getPrice(symbol);
    if (!price) return null;

    // Convert to requested denomination
    switch (denom) {
      case 'USD':
        return price.price;
      case 'USDC':
        return price.price; // 1:1 with USD
      case 'NIBI':
        const nibiPrice = await this.getPrice('NIBI');
        return nibiPrice ? price.price / nibiPrice.price : null;
      case 'ATOM':
        const atomPrice = await this.getPrice('ATOM');
        return atomPrice ? price.price / atomPrice.price : null;
      default:
        return price.price;
    }
  }

  // Update prices with realistic movements
  private updatePrices(): void {
    const now = new Date();
    const timeDiff = now.getTime() - this.lastUpdate.getTime();
    
    // Update prices every 5 minutes or on demand
    if (timeDiff > 5 * 60 * 1000 || Math.random() < 0.3) {
      Object.entries(MOCK_PRICES).forEach(([symbol, config]) => {
        const currentFeed = this.priceCache.get(symbol);
        if (currentFeed) {
          const newPrice = generatePriceFluctuation(currentFeed.price, config.volatility * 0.1);
          const change24h = generate24hChange(config.volatility);
          
          this.priceCache.set(symbol, {
            ...currentFeed,
            price: parseFloat(newPrice.toFixed(6)),
            change24h: parseFloat(change24h.toFixed(6)),
            changePercent24h: parseFloat(((change24h / config.basePrice) * 100).toFixed(2)),
            lastUpdated: now
          });
        }
      });
      this.lastUpdate = now;
    }
  }

  // Get historical prices (mock data)
  async getHistoricalPrices(symbol: string, days: number = 30): Promise<{ date: string; price: number }[]> {
    await new Promise(resolve => setTimeout(resolve, 200));
    
    const basePrice = MOCK_PRICES[symbol as keyof typeof MOCK_PRICES]?.basePrice || 1;
    const historical: { date: string; price: number }[] = [];
    
    for (let i = days; i >= 0; i--) {
      const date = new Date();
      date.setDate(date.getDate() - i);
      
      // Generate realistic historical trend with some randomness
      const trend = Math.sin(i * 0.1) * 0.1; // Cyclical trend
      const random = (Math.random() - 0.5) * 0.05; // Random noise
      const price = basePrice * (1 + trend + random);
      
      historical.push({
        date: date.toISOString().split('T')[0],
        price: parseFloat(price.toFixed(6))
      });
    }
    
    return historical;
  }

  // Get market summary
  async getMarketSummary(): Promise<{
    totalMarketCap: number;
    totalVolume24h: number;
    topGainers: string[];
    topLosers: string[];
  }> {
    const prices = await this.getPrices();
    
    const totalMarketCap = prices.data.reduce((sum, asset) => sum + asset.marketCap, 0);
    const totalVolume24h = prices.data.reduce((sum, asset) => sum + asset.volume24h, 0);
    
    // Sort by 24h change
    const sorted = [...prices.data].sort((a, b) => b.changePercent24h - a.changePercent24h);
    const topGainers = sorted.slice(0, 2).map(asset => asset.symbol);
    const topLosers = sorted.slice(-2).map(asset => asset.symbol);
    
    return {
      totalMarketCap,
      totalVolume24h,
      topGainers,
      topLosers
    };
  }

  // Simulate oracle failure (for testing error handling)
  async simulateFailure(): Promise<OracleResponse> {
    await new Promise(resolve => setTimeout(resolve, 100));
    
    return {
      success: false,
      data: [],
      timestamp: Date.now()
    };
  }
}

// Create singleton instance
export const mockOracle = new MockOracle();

// Export convenience functions
export const getPrices = () => mockOracle.getPrices();
export const getPrice = (symbol: string) => mockOracle.getPrice(symbol);
export const getPriceInDenom = (symbol: string, denom: string) => mockOracle.getPriceInDenom(symbol, denom);
export const getHistoricalPrices = (symbol: string, days?: number) => mockOracle.getHistoricalPrices(symbol, days);
export const getMarketSummary = () => mockOracle.getMarketSummary();
export const simulateOracleFailure = () => mockOracle.simulateFailure(); 