import { useState, useEffect, useCallback } from 'react';
import { 
  mockOracle, 
  PriceFeed, 
  OracleResponse, 
  getPrices, 
  getPrice, 
  getPriceInDenom,
  getHistoricalPrices,
  getMarketSummary
} from '../utils/mockOracle';

export interface UseOracleReturn {
  // Current prices
  prices: PriceFeed[];
  loading: boolean;
  error: string | null;
  
  // Market data
  marketSummary: {
    totalMarketCap: number;
    totalVolume24h: number;
    topGainers: string[];
    topLosers: string[];
  } | null;
  
  // Functions
  refreshPrices: () => Promise<void>;
  getAssetPrice: (symbol: string) => PriceFeed | undefined;
  getPriceInDenomination: (symbol: string, denom: string) => Promise<number | null>;
  getHistoricalData: (symbol: string, days?: number) => Promise<{ date: string; price: number }[]>;
  
  // Utilities
  formatPrice: (price: number, decimals?: number) => string;
  formatChange: (change: number, isPercent?: boolean) => string;
  formatVolume: (volume: number) => string;
  formatMarketCap: (marketCap: number) => string;
}

export const useOracle = (autoRefresh: boolean = true, refreshInterval: number = 30000): UseOracleReturn => {
  const [prices, setPrices] = useState<PriceFeed[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [marketSummary, setMarketSummary] = useState<UseOracleReturn['marketSummary']>(null);

  // Fetch prices from oracle
  const fetchPrices = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      
      const response = await getPrices();
      
      if (response.success) {
        setPrices(response.data);
        
        // Also fetch market summary
        const summary = await getMarketSummary();
        setMarketSummary(summary);
      } else {
        setError('Failed to fetch prices from oracle');
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Unknown error occurred');
    } finally {
      setLoading(false);
    }
  }, []);

  // Manual refresh function
  const refreshPrices = useCallback(async () => {
    await fetchPrices();
  }, [fetchPrices]);

  // Get specific asset price
  const getAssetPrice = useCallback((symbol: string): PriceFeed | undefined => {
    return prices.find(price => price.symbol === symbol);
  }, [prices]);

  // Get price in specific denomination
  const getPriceInDenomination = useCallback(async (symbol: string, denom: string): Promise<number | null> => {
    try {
      return await getPriceInDenom(symbol, denom);
    } catch (err) {
      console.error(`Error getting price for ${symbol} in ${denom}:`, err);
      return null;
    }
  }, []);

  // Get historical data
  const getHistoricalData = useCallback(async (symbol: string, days: number = 30) => {
    try {
      return await getHistoricalPrices(symbol, days);
    } catch (err) {
      console.error(`Error getting historical data for ${symbol}:`, err);
      return [];
    }
  }, []);

  // Format price with appropriate decimals
  const formatPrice = useCallback((price: number, decimals: number = 6): string => {
    if (price >= 1) {
      return `$${price.toFixed(2)}`;
    } else if (price >= 0.01) {
      return `$${price.toFixed(4)}`;
    } else {
      return `$${price.toFixed(6)}`;
    }
  }, []);

  // Format change with color indication
  const formatChange = useCallback((change: number, isPercent: boolean = false): string => {
    const sign = change >= 0 ? '+' : '';
    const value = isPercent ? change : change * 100;
    return `${sign}${value.toFixed(2)}${isPercent ? '%' : ''}`;
  }, []);

  // Format volume with appropriate units
  const formatVolume = useCallback((volume: number): string => {
    if (volume >= 1e9) {
      return `$${(volume / 1e9).toFixed(2)}B`;
    } else if (volume >= 1e6) {
      return `$${(volume / 1e6).toFixed(2)}M`;
    } else if (volume >= 1e3) {
      return `$${(volume / 1e3).toFixed(2)}K`;
    } else {
      return `$${volume.toFixed(2)}`;
    }
  }, []);

  // Format market cap with appropriate units
  const formatMarketCap = useCallback((marketCap: number): string => {
    if (marketCap >= 1e9) {
      return `$${(marketCap / 1e9).toFixed(2)}B`;
    } else if (marketCap >= 1e6) {
      return `$${(marketCap / 1e6).toFixed(2)}M`;
    } else if (marketCap >= 1e3) {
      return `$${(marketCap / 1e3).toFixed(2)}K`;
    } else {
      return `$${marketCap.toFixed(2)}`;
    }
  }, []);

  // Initial fetch
  useEffect(() => {
    fetchPrices();
  }, [fetchPrices]);

  // Auto-refresh setup
  useEffect(() => {
    if (!autoRefresh) return;

    const interval = setInterval(fetchPrices, refreshInterval);
    return () => clearInterval(interval);
  }, [autoRefresh, refreshInterval, fetchPrices]);

  return {
    prices,
    loading,
    error,
    marketSummary,
    refreshPrices,
    getAssetPrice,
    getPriceInDenomination,
    getHistoricalData,
    formatPrice,
    formatChange,
    formatVolume,
    formatMarketCap
  };
};

// Hook for single asset price
export const useAssetPrice = (symbol: string, autoRefresh: boolean = true) => {
  const [price, setPrice] = useState<PriceFeed | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchPrice = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      
      const assetPrice = await getPrice(symbol);
      if (assetPrice) {
        setPrice(assetPrice);
      } else {
        setError(`Asset ${symbol} not found`);
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Unknown error occurred');
    } finally {
      setLoading(false);
    }
  }, [symbol]);

  useEffect(() => {
    fetchPrice();
  }, [fetchPrice]);

  useEffect(() => {
    if (!autoRefresh) return;

    const interval = setInterval(fetchPrice, 30000); // 30 seconds
    return () => clearInterval(interval);
  }, [autoRefresh, fetchPrice]);

  return {
    price,
    loading,
    error,
    refresh: fetchPrice
  };
}; 