import React, { useState } from 'react';
import { useOracle, useAssetPrice } from '../hooks/useOracle';
import { simulateOracleFailure } from '../utils/mockOracle';

const OracleDemo: React.FC = () => {
  const [selectedAsset, setSelectedAsset] = useState('NIBI');
  const [days, setDays] = useState(7);
  
  const { 
    prices, 
    loading, 
    error, 
    marketSummary,
    refreshPrices,
    getHistoricalData,
    formatPrice,
    formatChange,
    formatVolume,
    formatMarketCap
  } = useOracle(true, 10000); // Refresh every 10 seconds for demo

  const { price: singleAssetPrice } = useAssetPrice(selectedAsset);

  const [historicalData, setHistoricalData] = useState<{ date: string; price: number }[]>([]);
  const [isLoadingHistorical, setIsLoadingHistorical] = useState(false);

  const handleGetHistorical = async () => {
    setIsLoadingHistorical(true);
    try {
      const data = await getHistoricalData(selectedAsset, days);
      setHistoricalData(data);
    } catch (err) {
      console.error('Error fetching historical data:', err);
    } finally {
      setIsLoadingHistorical(false);
    }
  };

  const handleSimulateFailure = async () => {
    try {
      await simulateOracleFailure();
      // Refresh to show the error state
      setTimeout(refreshPrices, 100);
    } catch (err) {
      console.error('Error simulating failure:', err);
    }
  };

  return (
    <div className="bg-gradient-to-r from-slate-800 to-slate-900 rounded-xl p-6 border border-slate-700">
      <div className="flex items-center justify-between mb-6">
        <div>
          <h2 className="text-xl font-bold text-white">Oracle Demo</h2>
          <p className="text-slate-400 text-sm">Test the mock oracle functionality</p>
        </div>
        <div className="flex space-x-2">
          <button
            onClick={refreshPrices}
            disabled={loading}
            className="px-3 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-slate-600 rounded-lg transition-colors text-sm font-medium"
          >
            {loading ? 'Refreshing...' : 'Refresh'}
          </button>
          <button
            onClick={handleSimulateFailure}
            className="px-3 py-2 bg-red-600 hover:bg-red-700 rounded-lg transition-colors text-sm font-medium"
          >
            Simulate Failure
          </button>
        </div>
      </div>

      {/* Error Display */}
      {error && (
        <div className="mb-6 p-4 bg-red-900/20 border border-red-700/50 rounded-lg">
          <div className="text-red-400 text-center">
            <div className="font-semibold mb-2">Oracle Error</div>
            <div className="text-sm">{error}</div>
          </div>
        </div>
      )}

      {/* Market Summary */}
      {marketSummary && (
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
          <div className="bg-slate-700/50 rounded-lg p-3">
            <div className="text-slate-400 text-xs">Total Market Cap</div>
            <div className="text-white font-semibold">{formatMarketCap(marketSummary.totalMarketCap)}</div>
          </div>
          <div className="bg-slate-700/50 rounded-lg p-3">
            <div className="text-slate-400 text-xs">24h Volume</div>
            <div className="text-white font-semibold">{formatVolume(marketSummary.totalVolume24h)}</div>
          </div>
          <div className="bg-slate-700/50 rounded-lg p-3">
            <div className="text-slate-400 text-xs">Top Gainer</div>
            <div className="text-green-400 font-semibold">{marketSummary.topGainers[0]}</div>
          </div>
          <div className="bg-slate-700/50 rounded-lg p-3">
            <div className="text-slate-400 text-xs">Top Loser</div>
            <div className="text-red-400 font-semibold">{marketSummary.topLosers[0]}</div>
          </div>
        </div>
      )}

      {/* Asset Selection and Historical Data */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
        {/* Asset Selection */}
        <div className="bg-slate-700/30 rounded-lg p-4">
          <h3 className="text-lg font-semibold text-white mb-4">Asset Details</h3>
          <div className="space-y-4">
            <div>
              <label className="block text-slate-400 text-sm mb-2">Select Asset</label>
              <select
                value={selectedAsset}
                onChange={(e) => setSelectedAsset(e.target.value)}
                className="w-full bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white"
              >
                {prices.map((asset) => (
                  <option key={asset.symbol} value={asset.symbol}>
                    {asset.symbol}
                  </option>
                ))}
              </select>
            </div>
            
            {singleAssetPrice && (
              <div className="bg-slate-700/50 rounded-lg p-3">
                <div className="text-slate-400 text-xs">Current Price</div>
                <div className="text-white text-lg font-bold">{formatPrice(singleAssetPrice.price)}</div>
                <div className={`text-sm ${
                  singleAssetPrice.changePercent24h >= 0 ? 'text-green-400' : 'text-red-400'
                }`}>
                  {formatChange(singleAssetPrice.changePercent24h, true)} (24h)
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Historical Data Controls */}
        <div className="bg-slate-700/30 rounded-lg p-4">
          <h3 className="text-lg font-semibold text-white mb-4">Historical Data</h3>
          <div className="space-y-4">
            <div>
              <label className="block text-slate-400 text-sm mb-2">Days</label>
              <select
                value={days}
                onChange={(e) => setDays(Number(e.target.value))}
                className="w-full bg-slate-700 border border-slate-600 rounded-lg px-3 py-2 text-white"
              >
                <option value={1}>1 Day</option>
                <option value={7}>7 Days</option>
                <option value={30}>30 Days</option>
                <option value={90}>90 Days</option>
              </select>
            </div>
            
            <button
              onClick={handleGetHistorical}
              disabled={isLoadingHistorical}
              className="w-full px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-slate-600 rounded-lg transition-colors text-sm font-medium"
            >
              {isLoadingHistorical ? 'Loading...' : 'Get Historical Data'}
            </button>
          </div>
        </div>
      </div>

      {/* Historical Data Display */}
      {historicalData.length > 0 && (
        <div className="bg-slate-700/30 rounded-lg p-4">
          <h3 className="text-lg font-semibold text-white mb-4">
            {selectedAsset} Price History ({days} days)
          </h3>
          <div className="overflow-x-auto">
            <table className="w-full text-sm">
              <thead>
                <tr className="text-slate-400 border-b border-slate-600">
                  <th className="text-left py-2">Date</th>
                  <th className="text-right py-2">Price</th>
                </tr>
              </thead>
              <tbody>
                {historicalData.map((item, index) => (
                  <tr key={index} className="border-b border-slate-700/50">
                    <td className="py-2 text-slate-300">{item.date}</td>
                    <td className="py-2 text-right text-white">{formatPrice(item.price)}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Current Prices Table */}
      <div className="bg-slate-700/30 rounded-lg p-4">
        <h3 className="text-lg font-semibold text-white mb-4">All Asset Prices</h3>
        <div className="overflow-x-auto">
          <table className="w-full text-sm">
            <thead>
              <tr className="text-slate-400 border-b border-slate-600">
                <th className="text-left py-2">Asset</th>
                <th className="text-right py-2">Price</th>
                <th className="text-right py-2">24h Change</th>
                <th className="text-right py-2">Volume</th>
                <th className="text-right py-2">Market Cap</th>
              </tr>
            </thead>
            <tbody>
              {prices.map((asset) => (
                <tr key={asset.symbol} className="border-b border-slate-700/50">
                  <td className="py-2 text-white font-medium">{asset.symbol}</td>
                  <td className="py-2 text-right text-white">{formatPrice(asset.price)}</td>
                  <td className={`py-2 text-right text-sm ${
                    asset.changePercent24h >= 0 ? 'text-green-400' : 'text-red-400'
                  }`}>
                    {formatChange(asset.changePercent24h, true)}
                  </td>
                  <td className="py-2 text-right text-slate-300">{formatVolume(asset.volume24h)}</td>
                  <td className="py-2 text-right text-slate-300">{formatMarketCap(asset.marketCap)}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default OracleDemo; 