import React from 'react';
import { useOracle } from '../hooks/useOracle';

const PriceTicker: React.FC = () => {
  const { prices, loading, formatPrice, formatChange } = useOracle(true, 15000); // Refresh every 15 seconds

  if (loading && prices.length === 0) {
    return (
      <div className="bg-slate-800/50 rounded-lg p-3 border border-slate-700">
        <div className="animate-pulse flex space-x-4">
          {[1, 2, 3].map((i) => (
            <div key={i} className="h-4 bg-slate-700 rounded w-20"></div>
          ))}
        </div>
      </div>
    );
  }

  return (
    <div className="bg-slate-800/50 rounded-lg p-3 border border-slate-700">
      <div className="flex items-center justify-between text-sm">
        <span className="text-slate-400 font-medium">Live Prices:</span>
        <div className="flex items-center space-x-4">
          {prices.map((asset) => (
            <div key={asset.symbol} className="flex items-center space-x-2">
              <span className="text-white font-medium">{asset.symbol}</span>
              <span className="text-white">{formatPrice(asset.price)}</span>
              <span className={`text-xs font-medium ${
                asset.changePercent24h >= 0 ? 'text-green-400' : 'text-red-400'
              }`}>
                {formatChange(asset.changePercent24h, true)}
              </span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default PriceTicker; 